import React from "react";
import { MachineDisplay } from "../MachineCollection/StoredMachine/MachineDisplay/MachineDisplay";
import { EditMachineStyle } from "./EditMachineStyle/EditMachineStyle";
import { Props } from "./Props";
import { connect } from "react-redux";
import { AppState, GetActiveMachine } from "../../Redux/Store";

const MachineMenuInternal: React.FC<Props> = (props) => {

    return (
        <div >
            {props.MachineType !== undefined &&
                <div style={{ display: "flex", flexDirection: "row" }}>
                    <MachineDisplay MachineType={props.MachineType} IsDefault={false} />
                    <EditMachineStyle />
                </div>}
        </div>
    );
};

const mapStateToProps = (state: AppState) => {
    const activeMachine = GetActiveMachine(state);

    return ({
        MachineType: activeMachine!
    });
};

export const MachineMenu = connect(
    mapStateToProps, {}
)(MachineMenuInternal);
