import { MachineInstanceModel } from "../../../../Models/MachineInstanceModel";
import { Draft } from "immer";
import { Actions } from "../../../Actions";
import { GetNewChannelSet } from "../../../../Helper/NewChannelSet";

export function MachineInstancesReducer(draft: Draft<MachineInstanceModel[]>, action: Actions): MachineInstanceModel[] {

    switch (action.type) {
        case "ChannelChangedActionType":
            const machine = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            machine.Channels[action.channelIndex] = action.channel;
            return draft;
        case "InputChannelChangedActionType":
            const machineInput = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            const channelModel = machineInput.InputChannels.find((ic) => ic.Direction === action.channel.Direction)!;
            channelModel.Direction = machineInput.InputChannels[action.channelIndex].Direction;
            machineInput.InputChannels[action.channelIndex] = action.channel;
            return draft;
        case "MachineRemovedActionType":
            return draft.filter((mi) => mi.Id !== action.machineInstanceId);
        case "MachineAddedActionType":

            const { outputChannels, inputChannels } = GetNewChannelSet(action.api);

            const maxId = (draft.length === 0) ? 0 : Math.max(...draft.map((mi) => mi.Id));
            const machineInstance: MachineInstanceModel = {
                Cell: action.cell,
                MachineId: action.machineId,
                Channels: outputChannels,
                InputChannels: inputChannels,
                Id: maxId + 1,
                Status: "Valid"
            };

            draft.push(machineInstance);
            return draft;
        case "MachineMovedActionType":
            const machineMoved = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            machineMoved.Cell = action.newCell;
            return draft;
        case "FixMachineActionType":
            const machineFixed = draft.find((mi) => mi.Id === action.machineInstanceId)!;
            if (machineFixed.Status === "Deleted") {
                return draft.filter((mi) => mi.Id !== action.machineInstanceId);
            } else if (machineFixed.Status === "ChannelsInvalid") {
                machineFixed.Channels = machineFixed.Channels.filter((c) => c.Status !== "ShouldNotExist").map((c) => c.Status === "ShouldExist" ? { ...c, Status: "Valid" } : c);
                machineFixed.Status = "Valid";
            }
            return draft;
        default:
            return draft;
    }
}