export const CleanMessage: (message: string) => [string, number] = (message: string) => {
    let starIndex = message.indexOf("*");
    if (starIndex !== -1) {
        const usedStars: number[] = [];
        let seekPosition = 0;
        while (true) {
            seekPosition = message.indexOf("<", seekPosition);
            if (seekPosition === -1) {
                break;
            }

            const lastIndex = message.indexOf(">", seekPosition);
            const numberPart = message.substring(seekPosition + 1, lastIndex);
            usedStars.push(parseInt(numberPart, 10));
            seekPosition = lastIndex;
        }

        let starNumber = 0;

        // eslint-disable-next-line
        while (usedStars.some((u) => (u === starNumber))) {
            starNumber++;
        }

        let returnMessage = message;
        do {
            returnMessage = returnMessage.substring(0, starIndex) + "<" + starNumber + ">" + returnMessage.substring(starIndex + 1);
            starIndex = returnMessage.indexOf("*");
        } while (starIndex !== -1);

        return [returnMessage, starNumber];
    }

    return [message, -1];
};

export const RestoreMessage = (message: string, starNumber: number) => {
    if (starNumber !== -1) {
        const seekString = "<" + starNumber + ">";
        const seekLength = seekString.length;
        // message = message.replace(seekString, "*");
        let seekPosition = 0;
        let index = message.indexOf(seekString);
        let indexStar = message.indexOf("*");

        while (index !== -1 || indexStar !== -1) {
            if (index !== -1 && (indexStar === -1 || index < indexStar)) {
                message = message.substring(0, index) + "*" + message.substring(index + seekLength);
                seekPosition = index + 1;
            } else {
                message = message.substring(0, indexStar) + seekString + message.substring(indexStar + 1);
                seekPosition = indexStar + seekLength;
            }

            index = message.indexOf(seekString, seekPosition);
            indexStar = message.indexOf("*", seekPosition);
        }
    }
    return message;
};