import { combineReducers, createStore } from "redux";
import { PlaySettingsReducer } from "./PlaySettings/Reducer";
import { LevelReducer } from "./Level/Reducer";
import { CustomMachinesReducer } from "./CustomMachines/Reducer";
import { CustomMachineModel } from "./CustomMachines/GridSetup/CustomMachineModel";
import { CustomMachinesActionTypes } from "./CustomMachines/Actions";
import { GridSetupActionTypes } from "./CustomMachines/GridSetup/Actions";
import { MachineInstanceActionTypes } from "./CustomMachines/GridSetup/MachineInstances/Actions";
import { LevelActionTypes } from "./Level/Actions";
import { PlaySettingActionTypes } from "./PlaySettings/Actions";

const autoReducer = combineReducers({
    PlaySettings: PlaySettingsReducer,
    CustomMachines: CustomMachinesReducer,
    Level: LevelReducer
});

export type AppState = ReturnType<typeof autoReducer>;

function rootReducer(state: AppState | undefined, action: CustomMachinesActionTypes | GridSetupActionTypes | MachineInstanceActionTypes | LevelActionTypes | PlaySettingActionTypes): AppState {
    const returnValue = autoReducer(state, action);
    window.localStorage.setItem("State", JSON.stringify(returnValue));
    return returnValue;
}

function getInitialState() {
    const storedState = window.localStorage.getItem("State");
    if (storedState === null || storedState === "") {
        return undefined;
    } else {
        return JSON.parse(storedState);
    }
}

export const AppStore = createStore(rootReducer, getInitialState(), (window as any).__REDUX_DEVTOOLS_EXTENSION__ && (window as any).__REDUX_DEVTOOLS_EXTENSION__());
export const AppState = () => {
    return AppStore.getState();
};

export function GetActiveMachine(state: AppState): CustomMachineModel | undefined {
    if (state.CustomMachines.activeMachineId === undefined) {
        return undefined;
    }
    const newActiveMachine = state.CustomMachines.machines.find((m) => m.Id === state.CustomMachines.activeMachineId);
    if (newActiveMachine === undefined) {
        throw Error("Getting an unknown machine, with id " + state.CustomMachines.activeMachineId);
    }
    return newActiveMachine;
}