
import { MachineInstancesReducer } from "./MachineInstances/Reducer";
import { CustomMachineModel } from "./CustomMachineModel";
import { Draft } from "immer";
import { Actions } from "../../Actions";

export function GridSetupReducer(draft: Draft<CustomMachineModel>, action: Actions): CustomMachineModel {
    // let returnValue: CustomMachineModel;

    if (draft === undefined) {
        return draft;
    }
    switch (action.type) {
        case "SetColumnNumberActionType":
            draft.Setup.NumberOfColumns = action.columns;
            return draft;
        case "SetRowNumberActionType":
            draft.Setup.NumberOfRows = action.rows;
            return draft;
        case "SetMessagesActionType":
            draft.Messages = action.messages;
            return draft;
        case "SetColorActionType":
            draft.Style.Color = action.color;
            return draft;
        case "SetBorderColorActionType":
            draft.Style.BorderColor = action.backgroundColor;
            return draft;
        case "SetNameActionType":
            draft.Style.Name = action.name;
            return draft;
        case "SetDescriptionActionType":
            draft.Style.Description = action.description;
            return draft;
        default:
            draft.Setup.MachineInstances = MachineInstancesReducer(draft.Setup.MachineInstances, action);
            return draft;
    }
}