import { Props } from "./Props";
import React from "react";
import { LoadLevel } from "../../../Redux/Level/Actions";
import { connect } from "react-redux";

const LevelPreviewInternal: React.FC<Props> = (props) => {
    const OnClick = () => props.LoadLevel(props.Level.Name);
    return (<div onClick={OnClick}>
        <label title={props.Level.Description}>{props.Level.Name}</label>
    </div>);
};

export const LevelPreview = connect(
    undefined,
    { LoadLevel }
)(LevelPreviewInternal);