import { UpdateShowAnimations, UpdateStepTime, SetComputing } from "./Actions";
import { PlaySettingsModel } from "./PlaySettingsModel";
import produce, { Draft } from "immer";
import { Actions } from "../Actions";

const initialState: PlaySettingsModel = {
    ShowAnimations: true,
    StepTime: 5,
    Computing: false
};
export const PlaySettingsReducer: (state: PlaySettingsModel | undefined, action: Actions) => PlaySettingsModel = produce((draft: Draft<PlaySettingsModel>, action: Actions) => {
    switch (action.type) {
        case UpdateShowAnimations:
            draft.ShowAnimations = action.show;
            break;
        case UpdateStepTime:
            draft.StepTime = action.stepTime;
            break;
        case SetComputing:
            draft.Computing = action.computing;
            break;
    }

    return;
}, initialState);
