import React from "react";
import { Props } from "./Props";
import { State } from "./State";
import { TestDisplay } from "./TestDisplay/TestDisplay";
import { AppState } from "../../Redux/Store";
import { connect } from "react-redux";
import { AddTest } from "../../Redux/Level/Actions";

class LevelDisplayInternal extends React.Component<Props, State> {

    public render() {
        const tests = this.props.Level.Tests.map((t, index) => <TestDisplay Test={t} key={index} />);
        const style: React.CSSProperties = { padding: 10 };

        return (
            <div style={style}>
                <div>
                    <b>{this.props.Level.Name}</b>
                    <br />
                    <i>{this.props.Level.Description}</i>
                </div>
                {tests}
                <button onClick={this.props.AddTest}>Add custom test</button>
            </div>
        );
    }
}

const mapStateToProps = (state: AppState) => {
    return {
        Level: state.Level
    };
};

export const LevelDisplay = connect(
    mapStateToProps,
    { AddTest }
)(LevelDisplayInternal);