import React from "react";
import { DirectionEnum } from "../../../../../Models/DirectionEnum";
import { Constants } from "../../../../Constants";
import { Props } from "./Props";

export class InputChannel extends React.Component<Props> {

    constructor(props: Props) {
        super(props);
        this.onClicked = this.onClicked.bind(this);
    }
    public render() {

        const cellSize = Constants.CellSize - 2;
        const marginFactor = 0.05;
        const height = 0.1;
        const border = 0.2;
        const channelStyle: React.CSSProperties = {
            position: "absolute",
            backgroundColor: "rgba(0,0,0,0)",
            pointerEvents: "visible",
        };

        switch (this.props.ChannelMap.Status) {
            case "ShouldExist":
                channelStyle.opacity = 0.5;
                channelStyle.backgroundColor = "white";
                break;
            case "ShouldNotExist":
                channelStyle.opacity = 0.4;
                break;
        }

        const borderStyle = "solid " + border * cellSize + "px " + this.props.ChannelMap.Channel;
        const borderStyleSide = "solid " + border * cellSize  + "px rgba(0,0,0,0)";
        const borderStyleNone = "solid 0px rgba(0,0,0,0)";
        channelStyle.borderBottom = borderStyleNone;
        channelStyle.borderLeft = borderStyleNone;
        channelStyle.borderTop = borderStyleNone;
        channelStyle.borderRight = borderStyleNone;

        const pasiveMargin = cellSize * (((1 - height) / 2) - border);
        const closeMargin = cellSize * (marginFactor);
        const farMargin = cellSize * (1 - border - marginFactor);

        switch (this.props.ChannelMap.Direction) {
            case DirectionEnum.Up:
                channelStyle.borderBottom = borderStyle;
                channelStyle.borderLeft = borderStyleSide;
                channelStyle.borderTop = borderStyleNone;
                channelStyle.borderRight = borderStyleSide;
                channelStyle.marginLeft = pasiveMargin;
                channelStyle.marginTop = farMargin;
                channelStyle.height = 0;
                channelStyle.width = cellSize * height;
                break;
            case DirectionEnum.Right:
                channelStyle.borderBottom = borderStyleSide;
                channelStyle.borderLeft = borderStyle;
                channelStyle.borderTop = borderStyleSide;
                channelStyle.borderRight = borderStyleNone;
                channelStyle.marginLeft = closeMargin;
                channelStyle.marginTop = pasiveMargin;
                channelStyle.height = cellSize * height;
                channelStyle.width = 0;
                break;
            case DirectionEnum.Down:
                channelStyle.borderBottom = borderStyleNone;
                channelStyle.borderLeft = borderStyleSide;
                channelStyle.borderTop = borderStyle;
                channelStyle.borderRight = borderStyleSide;
                channelStyle.marginLeft = pasiveMargin;
                channelStyle.marginTop = closeMargin;
                channelStyle.height = 0;
                channelStyle.width = cellSize * height;
                break;
            case DirectionEnum.Left:
                channelStyle.borderBottom = borderStyleSide;
                channelStyle.borderLeft = borderStyleNone;
                channelStyle.borderTop = borderStyleSide;
                channelStyle.borderRight = borderStyle;
                channelStyle.marginLeft = farMargin;
                channelStyle.marginTop = pasiveMargin;
                channelStyle.height = cellSize * height;
                channelStyle.width = 0;
                break;
            default:
                break;
        }

        return (
            <div style={channelStyle} onClick={this.onClicked}>
            </div>
        );
    }

    public onClicked(event: React.MouseEvent<HTMLDivElement, MouseEvent>): void {
        if (this.props.OnChanged !== undefined) {
            let newDirection: DirectionEnum = DirectionEnum.Left;
            switch (this.props.ChannelMap.Direction) {
                case DirectionEnum.Down:
                    newDirection = DirectionEnum.Left;
                    break;
                case DirectionEnum.Left:
                    newDirection = DirectionEnum.Up;
                    break;
                case DirectionEnum.Up:
                    newDirection = DirectionEnum.Right;
                    break;
                case DirectionEnum.Right:
                    newDirection = DirectionEnum.Down;
                    break;
            }

            this.props.OnChanged({ Direction: newDirection, Channel: this.props.ChannelMap.Channel, Status: this.props.ChannelMap.Status });
        }
    }
}