import { MachineApi } from "../Models/MachineApi";
import { ChannelModel } from "../Models/ChannelMapModel";
import { DirectionEnum } from "../Models/DirectionEnum";

export function GetNewChannelSet(api: MachineApi): {inputChannels: ChannelModel[], outputChannels: ChannelModel[]}{
    const outputChannels: ChannelModel[] = [];
    const inputChannels: ChannelModel[] = [];

    let direction: DirectionEnum = DirectionEnum.Up;
    for (const color of api.OutputChannels) {
        outputChannels.push({ Channel: color, Direction: direction, Status: "Valid" });
        direction++;
    }

    if (api.InputChannels.length > 1) {
        direction = 0;
        for (const color of api.InputChannels) {
            inputChannels.push({ Channel: color, Direction: direction, Status: "Valid" });
            direction++;
        }

        for (; direction < 4; direction++) {
            inputChannels.push({ Channel: api.InputChannels[0], Direction: direction, Status: "Valid" });
        }
    }

    return {inputChannels, outputChannels};
}