import React from "react";
import { Constants } from "../../../../Constants";
import { Props } from "./Props";

export class Machine extends React.Component<Props> {
    public render() {
        const machineSize = Constants.CellSize / 5 * 3;
        const borderSize = machineSize / 10;

        const machineStyle: React.CSSProperties = {
            height: this.props.IsDefault ? machineSize : machineSize * 0.9,
            width: this.props.IsDefault ? machineSize : machineSize * 0.9,
            backgroundColor: this.props.MachineStyle.Color,
            borderStyle: "solid",
            borderColor: this.props.MachineStyle.BorderColor,
            borderWidth: borderSize,
            borderRadius: this.props.IsDefault ? "50%" : "0%",
        };

        return (
            <div style={{ display: "flex", justifyContent: "center", alignItems: "center", height: Constants.CellSize - 2, width: Constants.CellSize - 2, pointerEvents: "none" }}>
                <div style={machineStyle} />
            </div>
        );
    }
}