import { PlaySettingActionTypes } from "./Actions";

export function UpdateStepTime(stepTime: number): PlaySettingActionTypes {
    return {
        type: "UpdateStepTime",
        stepTime
    };
}

export function UpdateShowAnimations(show: boolean): PlaySettingActionTypes {
    return {
        type: "UpdateShowAnimations",
        show
    };
}

export function StopComputing(): PlaySettingActionTypes{
    return {
        type: "SetComputing",
        computing: false
    };
}

export function StartComputing(): PlaySettingActionTypes{
    return {
        type: "SetComputing",
        computing: true
    };
}