import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { LevelModel } from "../LevelModel";

export const ReverseLimited: LevelModel = {
    AllowedMachines: [0, 5, 6, 7, 8, 9, 10, 11, 12],
    Description: "The machine should reverse the characters from the string. Input are only lowercase letters. With limited number of machines at your disposal, this is VERY HARD",
    Name: "ReverseLimited",
    Tests: [
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "abc" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "cba" }]
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "a" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "a" }]
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "" }]
        },
        {
            inputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "esrever" }],
            expectedOutputs: [{ Order: 0, Channel: ChannelColorEnum.Green, Content: "reverse" }]
        }
    ]
};