import React from "react";
import { LevelPreview } from "./LevelPreview/LevelPreview";
import { LevelStore } from "../../Levels/LevelStore";

const LevelList: React.FC<{}> = () => {
    const levelStore = LevelStore.GetStore();

    const levels = levelStore.GetAllLevels().map((l, index) => <LevelPreview Level={l} key={index}/>);

    return (
        <div>
            <b>Challenges:</b>
            {levels}
        </div>
    );
};

export default LevelList;
