import { LevelStore } from "../../Levels/LevelStore";
import { LevelActionTypes } from "./Actions";
import { LevelStateModel } from "./LevelStateModel";
import { TestStateModel } from "./TestStateModel";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { OrderedMessageModel } from "../../Models/OrderedMessageModel";
import produce, { Draft } from "immer";
import { Actions } from "../Actions";

const initialState = LoadLevel("IsEmpty");

export const LevelReducer: (state: LevelStateModel | undefined, action: Actions) => LevelStateModel = produce((draft: Draft<LevelStateModel>, action: Actions) => {
    switch (action.type) {
        case "LoadLevelActionType":
            return LoadLevel(action.name);
        case "AddTestActionType":
            const newTest: TestStateModel = {
                actualOutputs: [],
                expectedOutputs: [],
                inputs: [{ Channel: ChannelColorEnum.Green, Content: "", Order: 0 }],
                editable: true,
                id: Math.max(...draft.Tests.map((t) => t.id), -1) + 1
            };
            draft.Tests.push(newTest);
            return;
        case "RemoveTestActionType":
            draft.Tests = draft.Tests.filter((t) => t.id !== action.testId);
            return;
        case "ReportOutputActionType":
        case "ClearTestOutputActionType":
        case "AddInputActionType":
        case "RemoveInputActionType":
        case "UpdateInputActionType":
        case "UpdateOutputActionType":
        case "RemoveOutputActionType":
        case "AddOutputActionType":
            const tests = draft.Tests.map((t) => {
                if (t.id === action.testId) {
                    return UpdateTest(t, action);
                } else {
                    return t;
                }
            });
            draft.Tests = tests;
            return;
        default:
            return;
    }
}, initialState);

function LoadLevel(levelName: string): LevelStateModel {
    const levels = LevelStore.GetStore().GetAllLevels();
    const level = levels.find((l) => l.Name === levelName)!;
    const tests = level.Tests.length > 0 ? level.Tests.map((t, index) => ({ ...t, actualOutputs: [], id: index, editable: false })) :
        [{ actualOutputs: [], inputs: [{ Channel: ChannelColorEnum.Green, Content: "", Order: 0 }], expectedOutputs: [], id: 0, editable: true }];
    const returnValue: LevelStateModel = {
        AllowedMachines: level.AllowedMachines,
        Description: level.Description,
        Name: level.Name,
        Tests: tests
    };
    return returnValue;
}

function UpdateTest(test: TestStateModel, action: LevelActionTypes): TestStateModel {
    switch (action.type) {
        case "ReportOutputActionType":
            const outputs = test.actualOutputs.slice();
            outputs.push(action.outputMessage);
            return { ...test, actualOutputs: outputs };
        case "ClearTestOutputActionType":
            return { ...test, actualOutputs: [] };
        case "AddInputActionType":
            const maxOrder = Math.max(...test.inputs.map((i) => i.Order), -1) + 1;
            const newInput: OrderedMessageModel = {
                Channel: ChannelColorEnum.Green, Content: "", Order: maxOrder
            };
            return { ...test, inputs: test.inputs.concat(newInput) };
        case "RemoveInputActionType":
            const filteredInputs = test.inputs.filter((i) => i.Order !== action.order);
            const newInputs = filteredInputs.map((i, index) => ({ ...i, order: index }));

            const filteredOutputs = test.expectedOutputs.filter((i) => i.Order !== action.order);
            const newOutputs = filteredOutputs.map((o) => ({ ...o, Order: o.Order > action.order ? o.Order - 1 : o.Order }));
            return { ...test, inputs: newInputs, expectedOutputs: newOutputs, actualOutputs: test.actualOutputs.filter((a) => a.Order < action.order) };
        case "UpdateInputActionType":
            const inputsUpdated = test.inputs.map((i) => i.Order === action.input.Order ? action.input : i);

            return { ...test, inputs: inputsUpdated, actualOutputs: test.actualOutputs.filter((a) => a.Order < action.input.Order) };
        case "UpdateOutputActionType":
            const outputsUpdated = test.expectedOutputs.filter((i) => i.Order === action.output.Order).map((i, index) => index === action.index ? action.output : i);
            return { ...test, expectedOutputs: outputsUpdated };
        case "RemoveOutputActionType":
            const outputsRemoved = test.expectedOutputs.filter((i) => i.Order === action.order).filter((i, index) => index !== action.index);
            return { ...test, expectedOutputs: outputsRemoved };
        case "AddOutputActionType":
            const newArray = test.expectedOutputs.slice();
            let placeIndex = 0;
            for (const output of newArray) {
                if (output.Order > action.order) {
                    break;
                }
                placeIndex++;
            }
            newArray.splice(placeIndex, 0, { Order: action.order, Content: "", Channel: ChannelColorEnum.Green });

            return { ...test, expectedOutputs: newArray };
        default:
            return test;
    }
}
