import React from "react";
import { Constants } from "../Constants";
import { Props } from "./Props";
import { StoredMachine } from "./StoredMachine/StoredMachine";
import { AddNewMachine } from "../../Redux/CustomMachines/Actions";
import { connect } from "react-redux";
import { AppState, GetActiveMachine } from "../../Redux/Store";
import { MachineStore } from "../../MachineStore/MachineStore";
import { MachineId } from "../../MachineStore/MachineId";

const MachineCollectionInternal: React.FC<Props> = (props) => {

    const storedMachines = props.allowedMachines.map((m, index) => <div key={index}><StoredMachine machine={m} /></div>);

    return (
        <div style={{ display: "flex", flexFlow: "column" }}>
            <button onClick={props.AddNewMachine}>Create new machine</button>
            <div style={{ display: "flex", flexFlow: "column wrap", height: Constants.CellSize * 8 }}>
                {storedMachines}
            </div>
        </div>
    );
};

const mapStateToProps = (state: AppState) => {
    const store = MachineStore.GetStore();
    const machines = store.GetAllMachines();
    let allowedMachines: MachineId[] = [];
    const disallowedMachines: MachineId[] = [];

    for (const machine of machines) {
        if (state.Level.AllowedMachines.some((am) => machine.Id === am)) {
            allowedMachines.push(machine);
        } else {
            disallowedMachines.push(machine);
        }
    }

    const activeMachine = GetActiveMachine(state);
    if (activeMachine !== undefined) {
        const startMachines = activeMachine.Setup.MachineInstances.filter((m) => m.MachineId.Id < 5 && m.MachineId.Type === "Default");
        allowedMachines = allowedMachines.filter((m) => !startMachines.some((s) => s.MachineId.Id === m.Id && s.MachineId.Type === m.Type));
    }

    const allowedCustomMachines: MachineId[] = state.CustomMachines.machines.filter((customMachine) => !customMachine.DependingOn.some((dm) => dm.Type === "Default" && disallowedMachines.some((dis) => dis.Id === dm.Id))).map((cm) => ({ Id: cm.Id, Type: "Custom" }));
    allowedMachines = allowedMachines.concat(allowedCustomMachines);

    return {
        allowedMachines
    };
};

export const MachineCollection = connect(
    mapStateToProps,
    { AddNewMachine }
)(MachineCollectionInternal);