import { CustomMachinesListModel } from "./CustomMachinesListModel";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { DirectionEnum } from "../../Models/DirectionEnum";
import { GridSetupReducer } from "./GridSetup/Reducer";
import { CustomMachineModel } from "./GridSetup/CustomMachineModel";
import { MachineApi, ApiAreEqual } from "../../Models/MachineApi";
import { MachineId, MachineIdAreEqual } from "../../MachineStore/MachineId";
import { MachineInstanceModel } from "../../Models/MachineInstanceModel";
import produce, { Draft } from "immer";
import { Computer } from "../../Computing/Computer";
import { GetRandomColor } from "../../Helper/ColorHelper";
import { Actions } from "../Actions";

const initialState: CustomMachinesListModel = {
    machines: [],
    idSeed: 0,
    activeMachineId: undefined,
    deletedMachines: []
};

export const CustomMachinesReducer: (state: CustomMachinesListModel | undefined, action: Actions) => CustomMachinesListModel = produce((draft: Draft<CustomMachinesListModel>, action: Actions) => {
    switch (action.type) {
        case "AddNewMachineActionType":
            draft.idSeed++;
            const newMachine: CustomMachineModel = {
                Id: draft.idSeed,
                DirectlyDependingOn: [],
                Style: {
                    BorderColor: GetRandomColor(),
                    Color: GetRandomColor(),
                    Name: "NewMachine",
                    Description: "Enter your description here"

                },
                Api: {
                    InputChannels: [ChannelColorEnum.Green],
                    OutputChannels: []
                },
                Setup: {
                    NumberOfRows: 5,
                    NumberOfColumns: 6,
                    MachineInstances: [{
                        MachineId: { Id: 0, Type: "Default" },
                        Channels: [{ Direction: DirectionEnum.Right, Channel: ChannelColorEnum.Green, Status: "Valid" }],
                        InputChannels: [], Id: 1,
                        Cell: { Row: 2, Column: 0 },
                        Status: "Valid"
                    }]

                },
                ExitCells: [{ Cell: { Row: 2, Column: 5 }, Direction: DirectionEnum.Right }],
                Messages: [],
                DependingOn: [{ Id: 0, Type: "Default" }],
                DependingOnDeleted: [],
                Status: "Valid"
            };
            draft.machines.push(newMachine);
            draft.activeMachineId = draft.idSeed;
            break;
        case "LoadCustomMachineActionType":
            const newActiveMachine = draft.machines.find((m) => m.Id === action.machineId);
            if (newActiveMachine === undefined) {
                throw Error("Loading an unknown machine, with id " + action.machineId);
            }
            draft.activeMachineId = action.machineId;
            return;
        case "DeleteMachineActionType":
            draft.deletedMachines.push(draft.machines.find((m) => m.Id === action.id)!);
            draft.machines = draft.machines.filter((m) => m.Id !== action.id);
            if (draft.activeMachineId === action.id) {
                draft.activeMachineId = undefined;
            }

            const directlyDependentMachines = draft.machines.filter((m) => m.DirectlyDependingOn.some((dp) => dp === action.id));
            for (const dependentMachine of directlyDependentMachines) {
                const updatedMachines: MachineInstanceModel[] = dependentMachine.Setup.MachineInstances.map((mi) => MachineIdAreEqual(mi.MachineId, { Id: action.id, Type: "Custom" }) ? { ...mi, Status: "Deleted" } : mi);
                dependentMachine.Setup.MachineInstances = updatedMachines;
                dependentMachine.DirectlyDependingOn = dependentMachine.DirectlyDependingOn.filter((i) => i !== action.id);
            }

            const dependentMachines = draft.machines.filter((m) => m.DependingOn.some((dp) => dp.Id === action.id));
            for (const dependentMachine of dependentMachines) {
                dependentMachine.DependingOn = dependentMachine.DependingOn.filter((i) => !MachineIdAreEqual(i, { Id: action.id, Type: "Custom" }));
                dependentMachine.DependingOnDeleted.push(action.id);
            }

            break;
        case "LoadLevelActionType":
            draft.activeMachineId = undefined;
            return draft;
        default:
            if (draft.activeMachineId === undefined) {
                return draft;
            }

            const activeMachine = draft.machines.find((m) => m.Id === draft.activeMachineId)!;
            GridSetupReducer(activeMachine, action);
            break;
    }

    switch (action.type) {
        case "AddNewMachineActionType":
        case "ChannelChangedActionType":
        case "DeleteMachineActionType":
        case "FixMachineActionType":
        case "MachineAddedActionType":
        case "MachineMovedActionType":
        case "MachineRemovedActionType":
        case "SetColumnNumberActionType":
        case "SetRowNumberActionType":
            if (draft.activeMachineId !== undefined) {
                EnrichState(draft);
            }
            break;
    }

    return;
}, initialState);

const EnrichState = (draft: CustomMachinesListModel) => {

    DetermineDependencies(draft);
    SetApi(draft);

    return draft;
};

function DetermineDependencies(draft: Draft<CustomMachinesListModel>) {
    const dependingDirectly: number[] = [];
    const depending: MachineId[] = [];
    const DependingOnDeleted: number[] = [];
    const activeMachine = draft.machines.find((m) => m.Id === draft.activeMachineId)!;

    for (const machine of activeMachine.Setup.MachineInstances) {
        if (machine.Status === "Deleted") {
            DependingOnDeleted.push(machine.MachineId.Id);
            continue;
        }
        if (machine.MachineId.Type === "Custom") {
            if (!dependingDirectly.some((d) => d === machine.MachineId.Id)) {
                dependingDirectly.push(machine.MachineId.Id);
            }
        }
        if (!depending.some((d) => d.Id === machine.MachineId.Id && d.Type === machine.MachineId.Type)) {
            depending.push(machine.MachineId);
            if (machine.MachineId.Type === "Custom") {
                const machineType = draft.machines.find((m) => m.Id === machine.MachineId.Id)!;
                for (const depMachine of machineType.DependingOn) {
                    if (!depending.some((d) => d.Id === depMachine.Id && d.Type === depMachine.Type)) {
                        depending.push(depMachine);
                    }
                }
            }
        }
    }
    activeMachine.DependingOn = depending;
    activeMachine.DirectlyDependingOn = dependingDirectly;
    return draft;
}

function SetApi(draft: Draft<CustomMachinesListModel>) {
    const activeMachine = draft.machines.find((m) => m.Id === draft.activeMachineId)!;
    const computer = new Computer(activeMachine.Setup, true);
    activeMachine.ExitCells = computer.CalculateExits();

    const exitColors = activeMachine.ExitCells.map((ec) => {
        switch (ec.Direction) {
            case DirectionEnum.Right:
                return ChannelColorEnum.Blue;
            case DirectionEnum.Up:
                return ChannelColorEnum.Green;
            case DirectionEnum.Down:
                return ChannelColorEnum.Red;
            case DirectionEnum.Left:
                return ChannelColorEnum.Yellow;
            default:
                throw Error("Invalid value for direction");
        }
    });

    const outputs: ChannelColorEnum[] = [];
    for (const color of exitColors) {
        if (!outputs.some((o) => o === color)) {
            outputs.push(color);
        }
    }
    const startMachines = computer.GetStartMachines();
    const inputs = startMachines.map((mi) => {
        switch (mi.MachineId.Id) {
            case 0:
                return ChannelColorEnum.Green;
            case 1:
                return ChannelColorEnum.Red;
            case 2:
                return ChannelColorEnum.Blue;
            case 3:
                return ChannelColorEnum.Yellow;
            default:
                throw Error("Unknown startmachine " + mi.MachineId.Id);
        }
    });
    const newApi: MachineApi = { InputChannels: inputs, OutputChannels: outputs };
    if (!ApiAreEqual(newApi, activeMachine.Api)) {
        UpdateMachineStatusses(draft, newApi);
    }
    activeMachine.Api = newApi;
    CheckMachineStatus(activeMachine);
    return draft;
}

function CheckMachineStatus(draft: Draft<CustomMachineModel>) {
    if (draft.Setup.MachineInstances.some((m) => m.Status !== "Valid")) {
        draft.Status = "Invalid";
    } else {
        draft.Status = "Valid";
    }
}

function UpdateMachineStatusses(draft: Draft<CustomMachinesListModel>, api: MachineApi) {
    const activeMachineId = draft.activeMachineId!;
    const dependentMachines = draft.machines.filter((m) => m.DirectlyDependingOn.some((dp) => dp === activeMachineId));
    for (const dependentMachine of dependentMachines) {
        const machineInstances = dependentMachine.Setup.MachineInstances.filter((mi) => MachineIdAreEqual(mi.MachineId, { Id: activeMachineId, Type: "Custom" }));
        for (const machineInstance of machineInstances.filter((m) => m.Status !== "Deleted")) {
            // for (const newInputChannel of api.InputChannels) {
            //     if (!machineInstance.InputChannels.some((i) => i.Channel === newInputChannel)) {
            //         machineInstance.InputChannels.push({ Channel: newInputChannel, Direction: DirectionEnum.Up, Status: "ShouldExist" });
            //         machineInstanceValid = false;
            //     }
            // }
            // for (const inputChannel of machineInstance.InputChannels) {
            //     if (!api.InputChannels.some((i) => i === inputChannel.Channel)) {
            //         inputChannel.Status = "ShouldNotExist";
            //         machineInstanceValid = false;
            //     }
            // }

            //              Exists		Does Not Exist
            //            -------------------------------
            // Valid	 |	Valid		Should Not
            // Should	 |	Should		Does Not
            // ShouldNot |	Valid		Should Not
            // DoesNot	 |	Should		Does Not
            for (const newOutputChannel of api.OutputChannels) {
                const matchingOutput = machineInstance.Channels.find((i) => i.Channel === newOutputChannel);
                if (matchingOutput === undefined) {
                    machineInstance.Channels.push({ Channel: newOutputChannel, Direction: DirectionEnum.Up, Status: "ShouldExist" });
                } else if (matchingOutput.Status === "ShouldNotExist") {
                    matchingOutput.Status = "Valid";
                }
            }
            for (let index = 0; index < machineInstance.Channels.length; index++) {
                const outputChannel = machineInstance.Channels[index];
                if (!api.OutputChannels.some((i) => i === outputChannel.Channel)) {
                    if (outputChannel.Status === "Valid") {
                        outputChannel.Status = "ShouldNotExist";
                    } else if (outputChannel.Status === "ShouldExist") {
                        machineInstance.Channels.splice(index, 1);
                        index--;
                    }
                }
            }

            machineInstance.Status = machineInstance.Channels.every((c) => c.Status === "Valid") ? "Valid" : "ChannelsInvalid";
        }

        CheckMachineStatus(dependentMachine);
    }
}