import { GridSetupActionTypes } from "./GridSetup/Actions";

interface AddNewMachineActionType {
    type: "AddNewMachineActionType";
}

export function AddNewMachine(): CustomMachinesActionTypes {
    return {
        type: "AddNewMachineActionType",
    };
}

interface DeleteMachineActionType {
    type: "DeleteMachineActionType";
    id: number;
}

export function DeleteMachine(id: number): CustomMachinesActionTypes {
    return {
        type: "DeleteMachineActionType",
        id
    };
}

interface LoadCustomMachineActionType {
    type: "LoadCustomMachineActionType";
    machineId: number;
}

export function LoadCustomMachine(machineId: number): CustomMachinesActionTypes {
    return {
        type: "LoadCustomMachineActionType",
        machineId
    };
}

export type CustomMachinesActionTypes = AddNewMachineActionType | LoadCustomMachineActionType | DeleteMachineActionType | GridSetupActionTypes ;
