import React from "react";
import { Constants } from "../../../Constants";
import { Channel } from "../../../Grid/Cell/MachineInstance/Channel/Channel";
import { Machine } from "../../../Grid/Cell/MachineInstance/Machine/Machine";
import { Props } from "./Props";
import { GetNewChannelSet } from "../../../../Helper/NewChannelSet";
import { InputChannel } from "../../../Grid/Cell/MachineInstance/InputChannel/InputChannel";

export const MachineDisplay: React.FC<Props> = (props) => {

    const {inputChannels, outputChannels} = GetNewChannelSet(props.MachineType.Api);

    const channelComponents = outputChannels.map((c, index) => <Channel key={index} ChannelMap={c} />);
    const inputChannelComponents = inputChannels.map((c, index) => <div key={index}><InputChannel ChannelMap={c} IsDefault={props.IsDefault} /></div>);

    const absoluteStyle: React.CSSProperties = {
        position: "absolute",
    };

    const cellSize: React.CSSProperties = { height: Constants.CellSize, width: Constants.CellSize };
    return (
        <div >
            <label>{props.MachineType.Style.Name}</label>
            <div style={cellSize}>
                {inputChannelComponents}
                <div style={absoluteStyle}>
                    <Machine MachineStyle={props.MachineType.Style} IsDefault={props.IsDefault} />
                </div>
                <div style={absoluteStyle}>
                    {channelComponents}
                </div>
            </div>
        </div>);
};
