import React from "react";
import { connect } from "react-redux";
import { AppState, GetActiveMachine } from "../../Redux/Store";
import { Cell } from "./Cell/Cell";

import { Constants } from "../Constants";
import { Props } from "./Props";
import { GetLighterColor } from "../../Helper/ColorHelper";

class GridInternal extends React.Component<Props> {

    public render() {
        const rows = [];
        for (let rowIndex = 0; rowIndex < this.props.Rows; rowIndex++) {
            const row = [];
            for (let columnIndex = 0; columnIndex < this.props.Columns; columnIndex++) {

                row.push(<Cell key={columnIndex} cellId={{ Row: rowIndex, Column: columnIndex }} />);
            }
            rows.push(<div key={rowIndex} style={{ display: "flex", flexDirection: "row", opacity: 1 }}>{row}</div>);
        }

        return (
            <div style={{borderColor: GetLighterColor(this.props.Style.BorderColor, 0.4), borderWidth: Constants.CellSize * 0.2, borderStyle: "solid", marginBottom: 0 }}>
                <div style={{ display: "flex", flexDirection: "column", backgroundColor: GetLighterColor(this.props.Style.Color, 0.3)}}>
                    {rows}
                </div>
            </div>
        );
    }
}

function mapStateToProps(state: AppState): Props {
    const activeMachine = GetActiveMachine(state);

    return {
        Rows: activeMachine!.Setup.NumberOfRows,
        Columns: activeMachine!.Setup.NumberOfColumns,
        Style: activeMachine!.Style
    };
}

export const Grid = connect(
    mapStateToProps,
    {}
)(GridInternal);