import React from "react";
import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { AppState } from "../../Redux/Store";
import { Dashboard } from "../Dashboard/Dashboard";
import { Grid } from "../Grid/Grid";
import { LevelDisplay } from "../LevelDisplay/LevelDisplay";
import LevelList from "../LevelList/LevelList";
import { MachineCollection } from "../MachineCollection/MachineCollection";
import { MachineMenu } from "../MachineMenu/MachineMenu";
import { MenuItem } from "../MenuItem/MenuItem";
import { Settings } from "../Settings/Settings";
import { State } from "./State";
import { Computer } from "../../Computing/Computer";
import { AddNewMachine } from "../../Redux/CustomMachines/Actions";
import { connect } from "react-redux";
import { Props } from "./Props";
import { ClearMessages } from "../../Redux/CustomMachines/GridSetup/Actions";

class GameInternal extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.handleKeyPress = this.handleKeyPress.bind(this);

        this.state = {
            Computing: false,
            ShowingSetup: false,
        };
    }

    public componentDidMount(): void {
        document.addEventListener("keyup", (event) => this.handleKeyPress(event), false);
    }

    public componentWillUnmount() {
        document.removeEventListener("keyup", (event) => this.handleKeyPress(event), false);
    }

    private handleKeyPress(event: KeyboardEvent): void {
        switch (event.key) {
            case " ":
                Computer.StopComputation();
                this.props.ClearMessages();
                break;
            default:
                break;
        }
    }

    public render() {

        const allowedInputColors = [ChannelColorEnum.Green];

        return (
            <div style={{ display: "flex", flexFlow: "column wrap" }}>
                <div>
                    <h2 style={{ textAlign: "center" }}>LogicBoxes</h2>
                </div>
                <div style={{ display: "flex", flexFlow: "row wrap" }}>
                    <div style={{ flex: "1 1 200px" }}>
                        <MenuItem ItemName="Settings">
                            <Settings />
                        </MenuItem>
                        <MenuItem ItemName="Challenges">
                            <LevelList />
                        </MenuItem>
                        <LevelDisplay MultipleInputsAllowed={false} AllowedInputs={allowedInputColors} />

                    </div>
                    {AppState().CustomMachines.activeMachineId === undefined ?
                        <div style={{ display: "flex", flex: "4 1 400px", flexFlow: "column", alignItems: "center" }}>
                            <button title={"Add new machine"} onClick={this.props.AddNewMachine}>Create new machine</button>
                        </div>
                        :
                        <div style={{ display: "flex", flex: "4 1 400px", flexFlow: "column", alignItems: "center" }}>

                            <MachineMenu />
                            <div style={{ marginTop: 10 }}>
                                <Grid />
                            </div>
                            <div>
                                <Dashboard />
                            </div>
                        </div>
                    }
                    <div style={{ display: "flex", flexDirection: "column", flexWrap: "wrap", flex: "1 1 200px", marginLeft: 30 }}>
                        <MachineCollection />
                    </div>
                </div>
            </div >
        );
    }
}

const mapStateToProps = (state: AppState) => {
    return {
        HasActiveMachine: state.CustomMachines.activeMachineId !== undefined
    };
};
export const Game = connect(
    mapStateToProps,
    { AddNewMachine, ClearMessages }
)(GameInternal);