import { IActor } from "../MachineStore/IActor";
import { MachineStore } from "../MachineStore/MachineStore";
import { CellModel } from "../Models/CellModel";
import { ChannelColorEnum } from "../Models/ChannelColorsEnum";
import { DirectionEnum } from "../Models/DirectionEnum";
import { ExitCellModel } from "../Models/ExitCellModel";
import { MachineInstanceModel } from "../Models/MachineInstanceModel";
import { MessageLocationModel } from "../Models/MessageLocationModel";
import { MessageModel } from "../Models/MessageModel";
import { GridSetupModel } from "../Redux/CustomMachines/GridSetup/GridModel";
import { OrderedMessageModel } from "../Models/OrderedMessageModel";
import { ClearTestOutput, ReportOutput } from "../Redux/Level/Actions";
import { SetMessages } from "../Redux/CustomMachines/GridSetup/Actions";
import { IStore } from "../Redux/IStore";

export class Computer {
    private setup: GridSetupModel;
    private machines: Array<{ id: number, actor: IActor }> = [];
    private store: MachineStore;
    private outputs: MessageModel[] = [];
    private static cancelComputation: (() => void) | undefined = undefined;
    private static stepCount: number;
    private activeSetup: boolean;

    constructor(setup: GridSetupModel, activeSetup: boolean) {
        this.setup = setup;
        this.activeSetup = activeSetup;
        this.store = MachineStore.GetStore();
        this.HandleNextMessages = this.HandleNextMessages.bind(this);
        this.CalculateNextMessages = this.CalculateNextMessages.bind(this);
        this.RunInput = this.RunInput.bind(this);
        this.RunComputation = this.RunComputation.bind(this);
        this.SetDelay = this.SetDelay.bind(this);
    }

    public NewSetup(setup: GridSetupModel) {
        this.setup = setup;
    }

    public GetStartMachines(): MachineInstanceModel[] {
        const startMachines: MachineInstanceModel[] = [];
        for (const machineInstance of this.setup.MachineInstances) {
            if (machineInstance.MachineId.Id < 4 && machineInstance.MachineId.Type === "Default") {
                startMachines.push(machineInstance);
            }
        }

        return startMachines;
    }

    public GetStartMachine(color: ChannelColorEnum): MachineInstanceModel | undefined {

        let id: number = 0;
        switch (color) {
            case ChannelColorEnum.Green:
                id = 0;
                break;
            case ChannelColorEnum.Red:
                id = 1;
                break;
            case ChannelColorEnum.Blue:
                id = 2;
                break;
            case ChannelColorEnum.Yellow:
                id = 3;
                break;
        }

        return this.setup.MachineInstances.find((mi) => mi.MachineId.Id === id && mi.MachineId.Type === "Default");
    }

    public CalculateExits(): ExitCellModel[] {
        const exitCells: ExitCellModel[] = [];
        let cells: ExitCellModel[] = [];
        const starts = this.GetStartMachines();
        cells = cells.concat(starts.map((s) => ({ Cell: s.Cell, Direction: s.Channels[0].Direction })));
        for (let i = 0; i < cells.length; i++) {
            let cell: CellModel = cells[i].Cell;
            let machine: MachineInstanceModel | undefined;
            let cellModel: CellModel | undefined;
            while (machine === undefined) {
                cellModel = this.Move(cell, cells[i].Direction);
                if (cellModel === undefined) {
                    break;
                }

                cell = cellModel;
                // eslint-disable-next-line
                machine = this.setup.MachineInstances.find((mi) => mi.Cell.Column === cell!.Column && mi.Cell.Row === cell!.Row);
            }

            if (cellModel === undefined) {
                exitCells.push({ Direction: cells[i].Direction, Cell: cell });
                continue;
            }

            if (!cells.some((c) => c.Cell.Column === cell.Column && c.Cell.Row === cell.Row)) {
                cells = cells.concat(machine!.Channels.filter((c) => c.Status !== "ShouldExist").map((c) => ({ Cell: cell, Direction: c.Direction })));

            }
        }
        return exitCells;
    }

    public async RunComputation(testId: number, store: IStore): Promise<OrderedMessageModel[]> {
        Computer.StopComputation();
        this.machines = [];
        store.dispatch(ClearTestOutput(testId));
        const test = store.getState().Level.Tests.find((t) => t.id === testId);
        if (test === undefined) {
            return Promise.resolve([]);
        } else {
            let orderedOutputs: OrderedMessageModel[] = [];

            for (const input of test.inputs) {
                const results = await this.RunInput(input, store, testId, input.Order).catch((reason) => Promise.reject(reason));
                orderedOutputs = orderedOutputs.concat(results.map((mm) => ({ Content: mm.Content, Channel: mm.Channel, Order: input.Order })));
            }

            return Promise.resolve(orderedOutputs);
        }
    }
    public static StopComputation(): void {
        if (Computer.cancelComputation) {
            Computer.cancelComputation();
            Computer.cancelComputation = undefined;
        }

        Computer.stepCount = 0;
    }

    public RunInput(input: MessageModel, store?: IStore, testId?: number, order?: number): Promise<MessageModel[]> {
        const messageLocations: MessageLocationModel[] = [];
        const machineInstance = this.GetStartMachine(input.Channel);
        if (machineInstance !== undefined) {
            const direction = machineInstance.Channels[0].Direction;
            const messageLocation: MessageLocationModel = { Content: input.Content, Direction: direction, Cell: machineInstance.Cell };
            messageLocations.push(messageLocation);
        }

        if (store !== undefined && this.activeSetup && store.getState().PlaySettings.ShowAnimations) {
            store.dispatch(SetMessages(messageLocations));
        }

        return this.OrderNewCalculation({ messageLocations, store, testId, order });
    }

    private SetDelay<U, T>(callback: (args: U) => Promise<T>, args: U, timeout: number): Promise<T> {
        return new Promise<T>((resolve, reject) => {
            const clearHandle = window.setTimeout(() => callback(args).then(resolve).catch(reject), timeout);
            Computer.cancelComputation = () => {
                window.clearTimeout(clearHandle);
                reject("Computation cancelled");
            };
        });
    }

    private async HandleNextMessages(args: { messageLocations: MessageLocationModel[], store?: IStore, testId?: number, order?: number }): Promise<MessageModel[]> {

        const [nextMessages, outputMessages] = await this.CalculateNextMessages(args.messageLocations);
        if (outputMessages.length > 0) {
            this.outputs = this.outputs.concat(outputMessages);
        }

        if (args.store !== undefined) {
            for (const message of outputMessages) {
                args.store.dispatch(ReportOutput({ Channel: message.Channel, Content: message.Content, Order: args.order! }, args.testId!));
            }
        }

        if (args.store !== undefined && this.activeSetup && args.store.getState().PlaySettings.ShowAnimations) {
            args.store.dispatch(SetMessages(nextMessages));
        }

        if (nextMessages.length > 0) {
            return this.OrderNewCalculation({ ...args, messageLocations: nextMessages });
        } else {
            return Promise.resolve(this.outputs);
        }
    }

    private OrderNewCalculation(args: { messageLocations: MessageLocationModel[], store?: IStore, testId?: number, order?: number }): Promise<MessageModel[]> {
        Computer.stepCount++;
        if (this.activeSetup || Computer.stepCount % 100000 === 0) {
            const timeout = args.store !== undefined && args.store.getState().PlaySettings.ShowAnimations ? args.store.getState().PlaySettings.StepTime * 100 : 0;

            return this.SetDelay(this.HandleNextMessages, args, timeout);
        } else {
            return this.HandleNextMessages(args);
        }
    }

    private async CalculateNextMessages(messages: MessageLocationModel[]): Promise<[MessageLocationModel[], MessageModel[]]> {
        const doneMessages: MessageLocationModel[] = [];
        const nextMessages: MessageLocationModel[] = [];
        for (const message of messages) {
            let machine: MachineInstanceModel | undefined;
            let messageCell: CellModel | undefined = message.Cell;

            while (machine === undefined) {
                messageCell = this.Move(messageCell!, message.Direction);
                if (messageCell === undefined) {
                    break;
                }

                // eslint-disable-next-line
                machine = this.setup.MachineInstances.find((mi) => mi.Cell.Column === messageCell!.Column && mi.Cell.Row === messageCell!.Row);
            }

            if (messageCell === undefined) {
                doneMessages.push({ Cell: undefined, Direction: message.Direction, Content: message.Content });
                continue;
            }

            if (machine!.Status !== "Valid") {
                return Promise.reject("Encountered invalid machine, check your machines");
            }

            let channel = ChannelColorEnum.Green;
            const inputChannel = machine!.InputChannels.find((ic) => ic.Direction === message.Direction);
            if (inputChannel !== undefined) {
                channel = inputChannel.Channel;
            }

            let cachedMachine = this.machines.find((m) => m.id === machine!.Id);
            if (cachedMachine === undefined) {
                const machineInstance = this.setup.MachineInstances.find((mi) => mi.Id === machine!.Id)!;
                cachedMachine = { id: machineInstance.Id, actor: this.store.GetActor(machineInstance.MachineId) };
                this.machines.push(cachedMachine);
            }
            const actor = cachedMachine.actor;
            const nextContents = await actor.Act({ Content: message.Content, Channel: channel });
            for (const nextContent of nextContents) {
                const channelMap = machine!.Channels.find((cm) => cm.Channel === nextContent.Channel);
                if (channelMap === undefined) {
                    throw Error("Channelmap " + nextContent.Channel + "undefined");
                }

                const messageLocation: MessageLocationModel = { Cell: machine!.Cell, Content: nextContent.Content, Direction: channelMap.Direction };
                nextMessages.push(messageLocation);
            }
        }

        const outputs: MessageModel[] = [];
        for (const doneMessage of doneMessages) {
            switch (doneMessage.Direction) {
                case DirectionEnum.Up:
                    outputs.push({ Content: doneMessage.Content, Channel: ChannelColorEnum.Green });
                    break;
                case DirectionEnum.Down:
                    outputs.push({ Content: doneMessage.Content, Channel: ChannelColorEnum.Red });
                    break;
                case DirectionEnum.Right:
                    outputs.push({ Content: doneMessage.Content, Channel: ChannelColorEnum.Blue });
                    break;
                case DirectionEnum.Left:
                    outputs.push({ Content: doneMessage.Content, Channel: ChannelColorEnum.Yellow });
                    break;
            }
        }

        return [nextMessages, outputs];
    }

    private Move(cell: CellModel, direction: DirectionEnum): CellModel | undefined {
        switch (direction) {
            case DirectionEnum.Down:
                if (cell.Row === this.setup.NumberOfRows - 1) {
                    return;
                }
                return { Column: cell.Column, Row: cell.Row + 1 };
            case DirectionEnum.Up:
                if (cell.Row === 0) {
                    return;
                }
                return { Column: cell.Column, Row: cell.Row - 1 };
            case DirectionEnum.Right:
                if (cell.Column === this.setup.NumberOfColumns - 1) {
                    return;
                }
                return { Column: cell.Column + 1, Row: cell.Row };
            case DirectionEnum.Left:
                if (cell.Column === 0) {
                    return;
                }
                return { Column: cell.Column - 1, Row: cell.Row };
        }
    }
}