import React, { useState } from "react";
import { useDrag } from "react-dnd";
import { DragItemTypes } from "../../DragItemTypes";
import { MachineDisplay } from "./MachineDisplay/MachineDisplay";
import { Props } from "./Props";
import { MachineStore } from "../../../MachineStore/MachineStore";
import { LoadCustomMachine, DeleteMachine } from "../../../Redux/CustomMachines/Actions";
import { connect } from "react-redux";
import { AppState } from "../../../Redux/Store";
import { MachineId } from "../../../MachineStore/MachineId";
import { Constants } from "../../Constants";

const StoredMachineInternal: React.FC<Props> = (props) => {
    const [, drag] = useDrag({
        item: { type: DragItemTypes.NewMachine, id: props.machine },
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging(),
        }),
    });
    const [showMenu, changeShow] = useState(false);

    const machineType = MachineStore.GetStore().GetMachineType(props.machine);

    const isDefault = props.machine.Type === "Default";
    const machineStyle: React.CSSProperties = props.status === "Invalid" ? { borderBottom: Constants.CellSize / 18 + "px dotted orange", boxSizing: "border-box" } : {};

    return (
        <div ref={drag} title={machineType.Style.Description} onClick={() => changeShow(!showMenu)}>
            <div style={machineStyle}>
                <MachineDisplay MachineType={machineType} IsDefault={isDefault} />
            </div>
            {!isDefault && showMenu && <div style={{ display: "flex" }}>
                <button onClick={(event) => props.LoadCustomMachine(props.machine.Id)}>Edit</button>
                <button onClick={(event) => {
                    event.stopPropagation();
                    props.DeleteMachine(props.machine.Id);
                }}>Delete</button>
            </div>}
        </div>);
};

const mapStateToProps = (state: AppState, ownProps: { machine: MachineId }) => {
    return {
        status: ownProps.machine.Type === "Default" ? "Valid" : state.CustomMachines.machines.find((m) => m.Id === ownProps.machine.Id)!.Status
    };
};

export const StoredMachine = connect(
    mapStateToProps,
    {
        LoadCustomMachine, DeleteMachine
    }
)(StoredMachineInternal);