import React from "react";
import { GridSizeSetting } from "./GridSizeSetting/GridSizeSetting";
import { Props } from "./Props";
import { AppState } from "../../Redux/Store";
import { connect } from "react-redux";

export class SettingsInternal extends React.Component<Props> {

    public render() {
        return (
            <div>
                <div>
                    {/* <SaveMenu Setup={this.props.Setup} OnDataChanged={this.props.OnDataChanged} OnShowingSetup={this.props.OnShowingSetup} ShowSetup={this.props.ShowSetup} /> */}
                    {this.props.HasActiveMachine && <GridSizeSetting />}
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state: AppState) => {
    return {
        HasActiveMachine: state.CustomMachines.activeMachineId !== undefined
    };
};

export const Settings = connect(
    mapStateToProps,
    {
    }
)(SettingsInternal);