import React, { Dispatch } from "react";
import { useDrag } from "react-dnd";
import { connect } from "react-redux";
import { MachineStore } from "../../../../MachineStore/MachineStore";
import { ChannelModel } from "../../../../Models/ChannelMapModel";
import { ChangeChannel, MachineInstanceActionTypes, RemoveMachine, FixMachine, ChangeInputChannel } from "../../../../Redux/CustomMachines/GridSetup/MachineInstances/Actions";
import { AppState, GetActiveMachine } from "../../../../Redux/Store";
import { Constants } from "../../../Constants";
import { DragItemTypes } from "../../../DragItemTypes";
import { Channel } from "./Channel/Channel";
import { Machine } from "./Machine/Machine";
import { Props } from "./Props";
import { InputChannel } from "./InputChannel/InputChannel";

const MachineInstanceInternal: React.FC<Props> = (props) => {
    const [, drag] = useDrag({
        item: { type: DragItemTypes.MachineInstance, id: props.MachineInstanceId },
        end: (dropResult, monitor) => {
            if (!monitor.didDrop()) {
                props.OnRemoved();
            }
        },
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging(),
        }),
    });

    const OnClick = () => {
        if (props.MachineInstance.Status !== "Valid") {
            props.FixMachine();
        }
    };


    const channels = props.MachineInstance.Channels.map((c, index) => <div key={index}><Channel OnChanged={(channelMap) => props.ChangeChannel(index, channelMap)} ChannelMap={c} /></div>);
    let inputChannels: JSX.Element[] = [];
    if (props.MachineInstance.InputChannels.length > 1) {
        inputChannels = props.MachineInstance.InputChannels.map((c, index) => <div key={index}><InputChannel OnChanged={(channelMap) => props.ChangeInputChannel(index, channelMap)} ChannelMap={c} IsDefault={props.MachineInstance.MachineId.Type === "Default"} /></div>);
    }
    const absoluteStyle: React.CSSProperties = {
        position: "absolute",
        pointerEvents: "none"
    };

    const machineSize: React.CSSProperties = { height: Constants.CellSize - 2, width: Constants.CellSize - 2, margin: 1 };

    switch (props.MachineInstance.Status) {
        case "Deleted":
            machineSize.borderBottom = Constants.CellSize / 18 + "px dotted red";
            machineSize.boxSizing = "border-box";
            machineSize.opacity = 0.4;
            break;
        case "ChannelsInvalid":
            machineSize.borderBottom = Constants.CellSize / 18 + "px dotted orange";
            machineSize.boxSizing = "border-box";
            break;
    }

    const machineStyle = props.MachineStyle ?? MachineStore.GetStore().GetMachineType(props.MachineInstance.MachineId).Style;

    return (
        <div ref={drag} style={machineSize} onClick={OnClick}>
            {inputChannels}
            <div style={absoluteStyle}>
                <Machine MachineStyle={machineStyle} IsDefault={props.MachineInstance.MachineId.Type === "Default"} />
            </div>
            {channels}
        </div >

    );
};

const mapStateToProps = (state: AppState, ownProps: { machineInstanceId: number }) => {
    const activeMachine = GetActiveMachine(state);

    const MachineInstanceProp = activeMachine!.Setup.MachineInstances.find((mi) => mi.Id === ownProps.machineInstanceId)!;
    const machineType = MachineInstanceProp.MachineId.Type === "Default" ? undefined
        : MachineInstanceProp.Status === "Deleted" ? state.CustomMachines.deletedMachines.find((dm) => dm.Id === MachineInstanceProp.MachineId.Id)!.Style
            : state.CustomMachines.machines.find((m) => m.Id === MachineInstanceProp.MachineId.Id)!.Style;

    return {
        MachineInstance: MachineInstanceProp,
        MachineInstanceId: ownProps.machineInstanceId,
        MachineStyle: machineType
    };
};

const mapDispatchToProps = (dispatch: Dispatch<MachineInstanceActionTypes>, ownProps: { machineInstanceId: number }) => {
    return {
        ChangeChannel: (index: number, channel: ChannelModel) => dispatch(ChangeChannel(ownProps.machineInstanceId, index, channel)),
        ChangeInputChannel: (index: number, channel: ChannelModel) => dispatch(ChangeInputChannel(ownProps.machineInstanceId, index, channel)),
        OnRemoved: () => dispatch(RemoveMachine(ownProps.machineInstanceId)),
        FixMachine: () => dispatch(FixMachine(ownProps.machineInstanceId))
    };
};

export const MachineInstance = connect(
    mapStateToProps,
    mapDispatchToProps
)(MachineInstanceInternal);
