import { ChannelColorEnum } from "../../Models/ChannelColorsEnum";
import { MessageModel } from "../../Models/MessageModel";
import { IActor } from "../IActor";
import { IMachine } from "../IMachine";
import { IMachineType } from "../IMachineType";
import { MachineId } from "../MachineId";

export class SplitMachine implements IMachine {

    private actor: IActor = {
        Act: (message) => {
            const returnValue: MessageModel[] = [];

            returnValue.push({ Content: message.Content.slice(0, 1), Channel: ChannelColorEnum.Green });
            returnValue.push({ Content: message.Content.slice(1), Channel: ChannelColorEnum.Red });

            return Promise.resolve(returnValue);
        }
    };
    public GetActor(): IActor {
        return this.actor;
    }
    public GetType(): IMachineType {
        return {
            Style: {
                BorderColor: "Orange",
                Color: "Black",
                Name: "Split",

                Description: "The first character exits green, the other characters exit red",
            },
            Api: {
                InputChannels: [ChannelColorEnum.Green],
                OutputChannels: [ChannelColorEnum.Green, ChannelColorEnum.Red],

            },
            DependingOn: [],
            DirectlyDependingOn: []
        };
    }
    public Id: MachineId = {
        Id: 13,
        Type: "Default"
    };
}
