import React from "react";
import { Props } from "./Props";
import { AppState, GetActiveMachine } from "../../../Redux/Store";
import { SetName, SetDescription, SetColor, SetBorderColor } from "../../../Redux/CustomMachines/GridSetup/Actions";
import { connect } from "react-redux";

class EditMachineStyleInternal extends React.Component<Props> {
    public render() {
        return (
            <div style={{ display: "flex", flexDirection: "row" }}>

                <div style={{ display: "flex", flexDirection: "column" }}>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
                        <label>Name</label>
                        <input onChange={(event) => this.props.SetName(event.target.value)} value={this.props.MachineStyle.Name}></input>
                    </div>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>

                        <label>Description</label>
                        <input onChange={(event) => this.props.SetDescription(event.target.value)} value={this.props.MachineStyle.Description}></input>
                    </div>
                </div>
                <div style={{ display: "flex", flexDirection: "column" }}>

                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>

                        <label>Color</label>
                        <input onChange={(event) => this.props.SetColor(event.target.value)} value={this.props.MachineStyle.Color}></input>
                    </div>
                    <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between" }}>
                        <label>BorderColor</label>
                        <input onChange={(event) => this.props.SetBorderColor(event.target.value)} value={this.props.MachineStyle.BorderColor}></input>
                    </div>
                </div>

            </div >
        );
    }
}

const mapStateToProps = (state: AppState) => {
    const activeMachine = GetActiveMachine(state);

    return { MachineStyle: activeMachine!.Style };
};

export const EditMachineStyle = connect(
    mapStateToProps,
    { SetName, SetDescription, SetColor, SetBorderColor }
)(EditMachineStyleInternal);