import React from "react";
import { connect } from "react-redux";
import { UpdateShowAnimations, UpdateStepTime } from "../../Redux/PlaySettings/ActionCreators";
import { AppState } from "../../Redux/Store";
import NumberInput from "../NumberInput/NumberInput";
import { Props } from "./Props";

const DashboardInternal: React.FC<Props> = (props) => {

    const onClicked = () => {
        props.UpdateShowAnimations(!props.ShowAnimations);
    };
    return (
        <div style={{ display: "flex" }}>
            <div style={{ display: "flex", flexDirection: "column" }}>
                <label title="Show animations, will calculate as fast as possible when turned of">Show animations</label>
                <button onClick={onClicked}>{props.ShowAnimations ? "On" : "Off"}</button>
            </div>
            {props.ShowAnimations &&
                <div>
                    <label title="Step time in tenths of seconds, lower for faster animations">Step Time</label>
                    <NumberInput value={props.StepTime} valueChanged={props.UpdateStepTime} />
                </div>}
        </div>
    );
};

const mapStateToProps = (state: AppState) => {
    return {
        ShowAnimations: state.PlaySettings.ShowAnimations,
        StepTime: state.PlaySettings.StepTime
    };
};

export const Dashboard = connect(
    mapStateToProps,
    {
        UpdateStepTime, UpdateShowAnimations
    }
)(DashboardInternal);