import { MessageLocationModel } from "../../../Models/MessageLocationModel";
import { MachineInstanceActionTypes } from "./MachineInstances/Actions";

interface SetRowNumberActionType {
    type: "SetRowNumberActionType";
    rows: number;
}

export function SetRowNumber(rows: number): GridSetupActionTypes {
    return {
        type: "SetRowNumberActionType",
        rows
    };
}

interface SetColumnNumberActionType {
    type: "SetColumnNumberActionType";
    columns: number;
}

export function SetColumnNumber(columns: number): GridSetupActionTypes {
    return {
        type: "SetColumnNumberActionType",
        columns
    };
}

interface SetMessagesActionType {
    type: "SetMessagesActionType";
    messages: MessageLocationModel[];
}

export function SetMessages(messages: MessageLocationModel[]): GridSetupActionTypes {
    return {
        type: "SetMessagesActionType",
        messages
    };
}

export function ClearMessages(): GridSetupActionTypes {
    return {
        type: "SetMessagesActionType",
        messages: []
    };
}

interface SetColorActionType {
    type: "SetColorActionType";
    color: string;
}

export function SetColor(color: string): GridSetupActionTypes {
    return {
        type: "SetColorActionType",
        color
    };
}

interface SetBorderColorActionType {
    type: "SetBorderColorActionType";
    backgroundColor: string;
}

export function SetBorderColor(backgroundColor: string): GridSetupActionTypes {
    return {
        type: "SetBorderColorActionType",
        backgroundColor
    };
}
interface SetDescriptionActionType {
    type: "SetDescriptionActionType";
    description: string;
}

export function SetDescription(description: string): GridSetupActionTypes {
    return {
        type: "SetDescriptionActionType",
        description
    };
}

interface SetNameActionType {
    type: "SetNameActionType";
    name: string;
}

export function SetName(name: string): GridSetupActionTypes {
    return {
        type: "SetNameActionType",
        name
    };
}

export type GridSetupActionTypes =
    SetRowNumberActionType
    | SetColumnNumberActionType
    | SetMessagesActionType
    | SetColorActionType
    | SetBorderColorActionType
    | SetNameActionType
    | SetDescriptionActionType
    | MachineInstanceActionTypes;