import React from "react";
import { connect } from "react-redux";
import { SetColumnNumber, SetRowNumber } from "../../../Redux/CustomMachines/GridSetup/Actions";
import { AppState, GetActiveMachine } from "../../../Redux/Store";
import NumberInput from "../../NumberInput/NumberInput";
import { Props } from "./Props";

const GridSizeSettingInternal: React.FC<Props> = (props) => {

    return (
        <div>
            <label>Rows:</label>
            <NumberInput value={props.Rows} valueChanged={props.SetRowNumber} />
            <label>Columns:</label>
            <NumberInput value={props.Columns} valueChanged={props.SetColumnNumber} />
        </div>
    );
};

const mapStateToProps = (state: AppState) => {
    const activeMachine = GetActiveMachine(state);
    return {
        Rows: activeMachine!.Setup.NumberOfRows,
        Columns: activeMachine!.Setup.NumberOfColumns
    };
};

export const GridSizeSetting = connect(
    mapStateToProps,
    {
        SetRowNumber, SetColumnNumber
    }
)(GridSizeSettingInternal);